import React, { Component } from 'react'
import BirtdayInput from '../../../components/BirtdayInput'
import Footer from '../../../components/layout/Footer'
import {
    Link
} from "react-router-dom";

class InfoStep extends Component {

    constructor(props) {
        super(props)
        this._onClickSubmit = this._onClickSubmit.bind(this);
        this.state = {
            errorMsg: null
        }
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (this.props != nextProps) {
            if (nextProps.app.register.statusStep3 < 0){
                nextState.errorMsg = "ng k tht bi"
            }
        }
        return true;
    }


    _onClickSubmit() {
        let errorMsg = null;
        try {
            let name = this.nameInputRef.value;
            let password = this.passwordInputRef.value;
            let confirmPassword = this.confirmPasswordInputRef.value;

            if (!name) {
                errorMsg = 'Vui lng nhp h v tn'
                return;
            }

            let birthday = 0;
            let birthdayValue = this.birthdayInputRef.getValue();
            if (birthdayValue.error == 0) {
                birthday = birthdayValue.value;
            } else {
                errorMsg = birthdayValue.errorMsg;
                return;
            }

            if (!password) {
                errorMsg = 'Vui lng nhp mt khu'
                return;
            }
            if (!confirmPassword) {
                errorMsg = 'Vui lng xc nhn mt khu'
                return;
            }
            if (password != confirmPassword) {
                errorMsg = "Mt khu xc nhn khng ng"
                return;
            }
            let data = { ...this.props.app.register };
            data["birthday"] = parseInt(birthday / 1000);
            data["name"] = name;
            data["password"] = password;
            data["location"] = 1;
            this.props.appActions.updateCustomer(data);
        } finally {
            this.setState({errorMsg: errorMsg})
        }
    }

    render() {
        return (
            <div className="container-contact100 register_step3">
                <div className="wrap-contact100">
                    <div className="contact100-form validate-form">
                        <span className="contact100-form-title">
                            ng k
                     <div className="line-bt" />
                        </span>
                        <div className="form-description">Vui lng nhp thng tin  to ti khon</div>

                        <div className="form-row">
                            <input ref={e => this.nameInputRef = e} className="insee-input" type="text"  placeholder="H v tn" />
                        </div>
                        <div className="form-row">
                            <BirtdayInput ref={e => this.birthdayInputRef = e} />
                        </div>
                        <div className="form-row">
                            <select className="insee-input" >
                                <option>Khu vc xy dng</option>
                            </select>
                        </div>
                        <div className="form-row">
                            <input ref={e => this.passwordInputRef = e} className="insee-input" type="password" placeholder="Mt khu ng nhp" />
                        </div>
                        <div className="form-row">
                            <input ref={e => this.confirmPasswordInputRef = e}  className="insee-input" type="password" placeholder="Xc nhn mu khu" />
                        </div>
                        <div className="form-row prelative policy">
                            <input checked type="checkbox"/>
                            <span>
                                Ti  c v ng  cc&ensp;
                                <Link >
                                    iu khon s dng v chnh sch bo mt
                                </Link>
                                &ensp;ca cng ty
                            </span>
                        </div>
                        {this.state.errorMsg && <div className="msg-error"><span>*** {this.state.errorMsg}</span></div> }
                        <div className="btn-container">
                            <button onClick={this._onClickSubmit} className="btn-insee btn-insee-bg">ng k</button>
                        </div>
                    </div>
                    <div className="bg-desktop contact100-more flex-col-c-m"></div>
                </div>
                <Footer />
            </div>
        )
    }
}

export default InfoStep
